import { Component } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';

import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';

import { Platform } from 'ionic-angular';
import { Facebook } from '@ionic-native/facebook';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  displayName;
  photoURL;
  email;
  refreshToken;

  constructor(public navCtrl: NavController,
              private afAuth: AngularFireAuth, 
              private fb: Facebook, 
              private platform: Platform,
              public toastCtrl: ToastController)
               {
    afAuth.authState.subscribe((user: firebase.User) => {
      if (!user) {
        this.displayName = null;
        this.photoURL = null;
        this.email = null;
        this.refreshToken = null;
        return;
      }
      this.displayName = user.displayName;  
      this.photoURL = user.photoURL;    
      this.email = "Email: " + user.email;
      this.refreshToken = "Token: " + user.refreshToken;
    });
  }

  showToast(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000
    });
    toast.present();
  }

  signInWithFacebook() {
    if (this.platform.is('cordova')) {
      return this.fb.login(['email', 'public_profile']).then(res => {
        const facebookCredential = firebase.auth.FacebookAuthProvider.credential(res.authResponse.accessToken);
        return firebase.auth().signInWithCredential(facebookCredential);
      })
    }
    else {
      return this.afAuth.auth
        .signInWithPopup(new firebase.auth.FacebookAuthProvider()).then(res => {
          console.log(res);
          this.showToast("Seja Bem vindo " + res.user.displayName);
        })
    }
  }

  signOut() {
    this.afAuth.auth.signOut().then(res => {
      this.showToast("Logout feito com sucesso!");
    });
  }

}


